@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap);
body {
  margin: auto;
}

.page {
  margin: auto;
  height: 100%;
  min-height: 100vh;
  font-family: Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: space-between ;
}

.run {
  margin: auto;
}

.buttons {
  width: 90%;
  display: flex;
  justify-content: space-around;
}

.buttons button {
  width: 150px;
  height: 40px;
}

.grid {
  margin: 50px auto;
  padding: 0px;
  display: grid;
  max-width: 98vw;
  grid-template-rows: repeat(45, 25px);
  grid-template-columns: repeat(67, minmax(0, 25px));
  border: 1px solid #ddd;
}

.grid:hover {
  cursor: cell;
}

.squareContainer {
  width: 100%;
  height: 100%;
  display: flex;
  list-style-type: none;
  justify-content: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.square,
.startSquare,
.endSquare,
.wallSquare,
.weightSquare,
.visitedSquare,
.visitedFinishedSquare,
.visitedHeadSquare,
.visitedWeightSquare,
.visitedWeightFinishedSquare,
.pathSquare,
.pathWeightSquare,
.pathHeadSquare,
.pathFinishedSquare,
.pathFinishedWeightSquare {
  width: 100%;
  height: 100%;
  margin: auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.square:focus,
.startSquare:focus,
.endSquare:focus,
.wallSquare:focus,
.weightSquare:focus,
.visitedSquare:focus,
.visitedWeightSquare:focus,
.visitedHeadSquare:focus,
.visitedFinishedSquare:focus,
.visitedFinishedWeightSquare:focus,
.pathSquare:focus,
.pathWeightSquare:focus,
.pathHeadSquare:focus,
.pathFinishedSquare:focus,
.pathFinishedWeightSquare {
  outline: none;
}

.weightImg,
.startImg,
.endImg {
  width: 70%;
  height: 70%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: block;
}

.square,
.startSquare,
.endSquare {
  border: 0.1px solid #ddd;
}

@-webkit-keyframes wall {
  0% {
    background-color: rgb(38, 70, 83);
    transform: scale(1);
  }
  50% {
    background-color: rgb(38, 70, 83);
    transform: scale(1.2);
  }
  100% {
    background-color: rgb(38, 70, 83);
    transform: scale(1);
    border-radius: 0%;
  }
}

@keyframes wall {
  0% {
    background-color: rgb(38, 70, 83);
    transform: scale(1);
  }
  50% {
    background-color: rgb(38, 70, 83);
    transform: scale(1.2);
  }
  100% {
    background-color: rgb(38, 70, 83);
    transform: scale(1);
    border-radius: 0%;
  }
}

.wallSquare {
  -webkit-animation-name: wall;
          animation-name: wall;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

@-webkit-keyframes weight {
  0% {
    outline: none;
    transform: scale(1);
    border: 0.1px solid #ddd;
  }
  50% {
    outline: none;
    transform: scale(1.2);
    border: 0.1px solid #ddd;
  }
  100% {
    outline: none;
    transform: scale(1);
    border: 0.1px solid #ddd;
  }
}

@keyframes weight {
  0% {
    outline: none;
    transform: scale(1);
    border: 0.1px solid #ddd;
  }
  50% {
    outline: none;
    transform: scale(1.2);
    border: 0.1px solid #ddd;
  }
  100% {
    outline: none;
    transform: scale(1);
    border: 0.1px solid #ddd;
  }
}

.weightSquare {
  -webkit-animation-name: weight;
          animation-name: weight;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

@-webkit-keyframes visited {
  0% {
    background-color: rgb(116, 0, 184);
    transform: scale(0.3);
    border: none;
    border-radius: 50%;
    margin: auto;
  }
  25% {
    background-color: rgb(94, 96, 206);
    margin: auto;
  }
  50% {
    background-color: rgb(72, 191, 227);
    border-radius: 50%;
    margin: auto;
  }
  75% {
    transform: scale(1.2);
    background-color: rgb(128, 255, 219);
    border: none;
    border-radius: 0%;
    margin: auto;
  }
  75.1% {
    border: 1px solid rgb(173, 232, 244);
  }
  100% {
    transform: scale(1);
    background-color: rgb(86, 207, 225);
    border: 1px solid rgb(173, 232, 244);
    margin: auto;
  }
}

@keyframes visited {
  0% {
    background-color: rgb(116, 0, 184);
    transform: scale(0.3);
    border: none;
    border-radius: 50%;
    margin: auto;
  }
  25% {
    background-color: rgb(94, 96, 206);
    margin: auto;
  }
  50% {
    background-color: rgb(72, 191, 227);
    border-radius: 50%;
    margin: auto;
  }
  75% {
    transform: scale(1.2);
    background-color: rgb(128, 255, 219);
    border: none;
    border-radius: 0%;
    margin: auto;
  }
  75.1% {
    border: 1px solid rgb(173, 232, 244);
  }
  100% {
    transform: scale(1);
    background-color: rgb(86, 207, 225);
    border: 1px solid rgb(173, 232, 244);
    margin: auto;
  }
}

.visitedSquare,
.visitedWeightSquare {
  -webkit-animation-name: visited;
          animation-name: visited;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.visitedFinishedSquare,
.visitedFinishedWeightSquare {
  background-color: rgb(86, 207, 225);
  border: 1px solid rgb(173, 232, 244);
}

.visitedHeadSquare {
  background-color: rgb(255, 255, 63);
  border: 1px solid #eee;
}

@-webkit-keyframes path {
  0% {
    transform: scale(0.65);
    background-color: rgb(255, 255, 63);
    border-radius: 30%;
    margin: auto;
  }
  100% {
    transform: scale(1);
    background-color: rgb(255, 255, 63);
    border-radius: 0%;
    margin: auto;
  }
}

@keyframes path {
  0% {
    transform: scale(0.65);
    background-color: rgb(255, 255, 63);
    border-radius: 30%;
    margin: auto;
  }
  100% {
    transform: scale(1);
    background-color: rgb(255, 255, 63);
    border-radius: 0%;
    margin: auto;
  }
}

.pathSquare,
.pathWeightSquare,
.pathHeadSquare {
  -webkit-animation-name: path;
          animation-name: path;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.pathFinishedSquare,
.pathFinishedWeightSquare {
  background-color: yellow;
  margin: auto;
}

.topBar {
  margin: 0px auto;
  box-sizing: border-box;
  background-color: rgb(38, 70, 83);
  width: 100%;
  height: 100%;
  min-width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.title {
  font-family: 'Mulish', sans-serif;
  font-size: 30px;
  font-weight: 1000;
  color: rgb(173, 232, 244);
  background-color: transparent;
  margin: 10px auto;
}

.optionsContainer {
  display: flex;
  justify-content: space-between;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  width: 80%;
  margin: auto;
}

.topBarItemContainer {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  margin: 10px auto;
}

.algorithmForm {
  display: inline-flex;
  flex-wrap: nowrap;
}

.topBarButton,
.topBarButtonWhileAnimating {
  border: none;
  border-radius: 5px;
  height: 40px;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  font-weight: 300;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-left: 10px;
  margin-right: 10px;
}

.topBarButton {
  color: rgb(38, 70, 83);
  background-color: rgb(233, 196, 106);
}

.topBarButton:hover {
  cursor: pointer;
  background-color: rgb(42, 157, 143);
  color: white;
}

.resetButton:hover {
  background-color: rgb(231, 111, 81);
}

.topBarButtonWhileAnimating,
.topBarButtonWhileAnimating:hover {
  background-color: rgb(233, 196, 106);
  color: rgb(38, 70, 83);
}

.topBarButton:focus,
.topBarButtonWhileAnimating:focus {
  outline: none;
}

